import React from "react";
import moment from "moment";
import API2 from "./utils/API2";
import Navbar from "./components/Navbar";
import EmployeeTable from "./components/EmployeeTable";
import Footer from "./components/Footer";

class App extends React.Component {
  state = {
    employee: [],
    filtered: [],
    search: " ",
    sort: true,
  };
  // call to get the information from the api
  componentDidMount() {
    API2.getUsers().then((response) => {
      this.setState({
        employee: response.data.results,
        filtered: response.data.results,
      });
    });
  }
  // creating the search bar event for the employees
  handleInputChange = (event) => {
    // console.log(event.target.value);
    const value = event.target.value;
    const employeeFilter = this.state.employee.filter((data) => {
      let compare = Object.values(data).join("").toLowerCase();
      return compare.indexOf(value.toLowerCase()) !== -1;
    });
    this.setState({ filtered: employeeFilter });
  };
  //  I want to make the table headers clickable for the sort.
  //   const onHeaderClick = () => {
  //     return {
  //       onClick: () => {
  //         // do something
  //       },
  //     };
  // };
  // creating the function to sort employees
  //using the ternary operator
  handleSortByName = () => {
    const sortEmployees = this.state.employee.sort((a, b) =>{
      // console.log(a, b)
       return a.name.first > b.name.first ? 1 : -1
    }
    )
    console.log(sortEmployees)
    this.setState({ results: sortEmployees });

    switch (this.state.sort) {
      case true:
        this.setState({ sort: false });
        break;
      case false:
        this.setState({ sort: true });
        sortEmployees.reverse();
        break;

      default:
        console.log("Something isn't right");
        break;
    }
  };
  // {/* <th scope="col"{...clickableHeader(column)}>Name</th> */}

  render() {
    return (
      //out the table head
      <div className="container-flex">
        <Navbar handleInputChange={this.handleInputChange} />
        <table className="table table-dark">
          {/* // clickableHeaders ={onHeaderClick}> */}
          <thead className="thead-light">
            <tr>
              <th scope="col">Image</th>
              <th scope="col" onClick= {()=>{
                this.handleSortByName()
              }}>Name</th>
              <th scope="col">Email</th>
              <th scope="col">phone</th>
              <th scope="col">DOB</th>
            </tr>
          </thead>
          {this.state.filtered.map((employee) => (
            <EmployeeTable
              key={employee.login.uuid}
              image={employee.picture.thumbnail}
              firstName={employee.name.first}
              lastName={employee.name.last}
              email={employee.email}
              phone={employee.phone}
              Dob={moment(employee.dob.date).format("MM-DD-YYYY")}
            />
          ))}
        </table>
        <Footer />
      </div>
    );
  }
}

export default App;
